import React from 'react';
import './CircleSelector.css';
const CircleSelector = (props) => (
    <div className="CircleSelector">
      <button className={props.idx == 1 ? "selected" : ""} onClick={() => props.circleClick(1)}>{props.idx == 1 ? "CIRCLE 1 SELECTED" : "SELECT CIRCLE 1"}</button>
      <button className={props.idx == 2 ? "selected" : ""} onClick={() => props.circleClick(2)}>{props.idx == 2 ? "CIRCLE 2 SELECTED" : "SELECT CIRCLE 2"}</button>
      <button className={props.idx == 3 ? "selected" : ""} onClick={() => props.circleClick(3)}>{props.idx == 3 ? "CIRCLE 3 SELECTED" : "SELECT CIRCLE 3"}</button>
      <button className={props.idx == 4 ? "selected" : ""} onClick={() => props.circleClick(4)}>{props.idx == 4 ? "CIRCLE 4 SELECTED" : "SELECT CIRCLE 4"}</button>
    </div>
  );
  
  export default CircleSelector;